.contacts-page {
    display: flex;
    justify-content: space-between;
    padding-bottom: 60px;
    &__info {
        flex-grow: 1;
        margin-right: 45px;
        &-item {
            &:not(:last-child) {
                border-bottom: 1px dashed var(--Gainsboro);
                margin-bottom: 25px;
            }
        }
        h3 {
            margin: 0 0 20px;
            font-size: 13px;
        }
        p {
            margin: 0 0 25px;
            line-height: 21px;
        }
        ul {
            margin-bottom: 25px;
        }
        li {
            &:not(:last-child) {
                margin-bottom: 10px;
            }
        }
        a {
            margin-bottom: 28px;
        }
    }
    &__map {
        width: 48.5vw;
        height: 516px;
        padding: 30px;
        border: 1px solid var(--Gainsboro);
        flex-shrink: 1;
        &>div {
            width: 100%;
            height: 100%;
        }
    }
}
.contacts-writetous {
    padding-bottom: 60px;
    margin-top: -5px;
    max-width: 935px;
    width: 100%;
    .alert {
        margin-bottom: 40px;
        line-height: 1.4;
    }
    .attachedFile {
        margin: -10px 0 30px;
        p {
            margin-top: 15px;
        }
    }
    .bottom {
        border-top: 1px dashed var(--Gainsboro);
        p {
            margin: 25px 0 30px;
            line-height: 1.4;
        }
        button {
            min-width: 150px;
            padding: 10px 26px
        }
    }
    .input-List {
        margin-bottom: 35px;
    }
    .input-wrap {
        flex-direction: row-reverse;
        margin-bottom: 20px;
    }
    .input {
        padding: 11px 15px;
        flex-shrink: 1;
    }
    .input-name {
        position: static;
        display: inline-block;
        width: 104px;
        margin-right: 20px;
        margin-top: 11px;
        flex-shrink: 0;
        span {
            color: var(--Red);
        }
    }
    span.Btn {
        padding: 10px 21.5px;
    }
    textarea {
        height: 100px;
    }
    .body>p {
        line-height: 1.4;
    }
}
.contacts-feedback {
    .bottom {
        border-top: none;
    }
    .input-name {
        width: 91px;;
    }
}
@media(max-width: 991px) {
    .contacts-writetous {
        padding-bottom: 30px;
        .alert {
            margin-bottom: 25px;
        }
        .input-List {
            margin-bottom: 30px;
        }
        .body>p {
            margin-top: 8px;
        }
        .bottom p {
            margin: 22px 0 25px;
        }
    }
}
@media(max-width: 850px) {
    .contacts-page {
        flex-direction: column;
        padding-bottom: 30px;
        &__info {
            margin-right: 0;
        }
        &__map {
            padding: 24px;
            height: 410px;
            width: 100%;
        }
    }
}
@media(max-width: 767px) {
    .contacts-page {
        margin-top: -5px;
    }
}
@media(max-width: 600px) {
    .contacts-writetous {
        .input-name {
            font-size: 14px;
        }
    }

}
@media(max-width: 575px) {
    .contacts-page {
        &__info {
            h3 {
                margin-bottom: 10px;
            }
            p {
                margin-bottom: 18px;
            }
            ul {
                margin-bottom: 18px;
            }
            a {
                margin-bottom: 20px;
            }
            &-item:not(:last-child) {
                margin-bottom: 15px;
            }
        }
        &__map {
            padding: 14px;
            margin-top: 10px;
        }
    }
    .contacts-writetous {
        p {
            font-size: 14px;
        }
        .alert {
            margin-bottom: 15px;
        }
        .input-wrap {
            flex-direction: column;
            input, textarea {
                order: 2;
            }
            &>span {
                order: 1;
                margin: 0 0 12px;
            }
        }
        .input-List {
            margin-bottom: 20px;
        }
        .body>p {
            margin-bottom: 5px;
        }
        .bottom p {
            margin: 18px 0 20px;
        }
        .attachedFile {
            margin: -6px 0 20px
        }
    }
}